import { Injectable } from '@angular/core';
import * as S3 from 'aws-sdk/clients/s3';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UploadFileService {

  constructor() { }
  uploadFile(file: any, cb: any) {
    const contentType = file.type;
    const bucket = new S3(
      {
        accessKeyId: environment.AWS_ACCESS_KEY_ID,
        secretAccessKey: environment.AWS_SECRET_ACCESS_KEY,
        region: environment.AWS_S3_REGION_NAME
      }
    );
    const params = {
      Bucket: environment.AWS_STORAGE_BUCKET_NAME,
      Key: 'conversation/' + file.name,
      Body: file,
      ACL: 'public-read',
      ContentType: contentType
    };
    bucket.upload(params, function (err: any, data: any) {
      if (err) {
        cb(false);
      }
      cb(data.Location);
    });
  }
  savePageDetail(url: any, page: any) {
    var detail = {
      url: url,
      page: page
    }
    localStorage.setItem('previousPageDetail', JSON.stringify(detail));
  }
  getPageDetail() {
    const jsonData = localStorage.getItem('previousPageDetail');
    if (jsonData !== null) {
      try {
        var pageDetail = JSON.parse(localStorage.getItem('previousPageDetail') || '');
        if (pageDetail.url == window.location.href) {
          localStorage.removeItem('previousPageDetail');
          return pageDetail.page;
        }
        else {

          localStorage.removeItem('previousPageDetail');
          return false;
        }
      } catch (error) {
        return false;
      }
    }
  }
}
